/* # =================================================================
   # Global selectors
   # ================================================================= */

html {
	box-sizing: border-box;
	overflow-y: scroll;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

*,
::before,
::after {
	box-sizing: inherit;
}

::before,
::after {
	text-decoration: inherit;
	vertical-align: inherit;
}

* {
	background-repeat: no-repeat;
	padding: 0;
	margin: 0;
}

/* # =================================================================
   # General elements
   # ================================================================= */

audio:not([controls]) {
	display: none;
	height: 0;
}

hr {
