	color: #000;
}
.serif {
	font-family: 'Times New Roman', '游明朝', YuMincho, 'ヒラギノ明朝 ProN W3', 'Hiragino Mincho ProN', 'HG明朝E', '小塚明朝 ProN', 'メイリオ', Meiryo, serif;
}
h1, h2, h3, h4, h5, h6 {
font-weight: normal;
font-size: 100%;
}

/* =Print
----------------------------------------------- */
@media print {
  body {
    background: none !important;
    color: #000;
    font-size: 10pt;
  }

  a[rel=bookmark]:link:after,
  a[rel=bookmark]:visited:after {
    content: " [" attr(href) "] ";
    /* Show URLs */
  }

}


